.main-cart {
    position: relative;
    width: 53vw;
    height: 70vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: row;
            flex-direction: row;
    background-color: rgba(0, 0, 0, 0.65);
    z-index: 999;
    -webkit-animation-name: fadeIn;
    animation-name: fadeIn;
    overflow: hidden;
}

.animated {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
  }

@-webkit-keyframes fadeIn {
    from {
      opacity: 0;
      -webkit-transform: translateY(10px);
              transform: translateY(10px);
    }
  
    to {
      opacity: 1;
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
}

@keyframes fadeIn {
    from {
      opacity: 0;
      -webkit-transform: translateY(10px);
              transform: translateY(10px);
    }
  
    to {
      opacity: 1;
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
}

.cart-poster {
    position: relative;
    width: 45%;
    height: 100%;
    vertical-align: middle;
    background-color: rgb(0, 0, 0);
}

.cart-poster img {
    position: relative;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.cart-info {
    position: relative;
    width: 55%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 10px 20px;
}

.additional-details {
    position: relative;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.cart-info h1 {
    font-size: 45px;
    text-transform: uppercase;
    margin: 20px 0;
    color: var(--color-font);
    font-family: var(--title-font);
    text-align: left;
    overflow: hidden;
    text-overflow: clip;
    height: auto;
    line-height: 45px;
    -webkit-line-clamp: 3;
    display: -webkit-box;
    -webkit-box-orient: vertical;
}

.tagline {
    font-size: 20px;
    text-align: left;
    margin-bottom: 5px;
    color: var(--color-font-special);
    font-family: var(--special-font);
}

.overview {
    font-size: 15px;
    text-align: left;
    margin-bottom: 20px;
    color: var(--color-font);
    font-family: var(--font);
    overflow: hidden;
    text-overflow: clip;
    height: auto;
    line-height: 20px;
    -webkit-line-clamp: 4;
    display: -webkit-box;
    -webkit-box-orient: vertical;
}

.genre-list {
    font-size: 24px;
    text-align: left;
    color: var(--color-font-special);
    font-family: var(--special-font);
    
}

.production-list {
    font-size: 18px;
    text-align: left;
    margin-bottom: 5px;
    color: var(--color-font);
    font-family: var(--font);
    overflow: hidden;
    text-overflow: clip;
    height: auto;
    line-height: 20px;
    -webkit-line-clamp: 2;
    display: -webkit-box;
    -webkit-box-orient: vertical;
}

.release-details {
    position: relative;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.release-details-left, .release-details-right {
    width: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-flex-direction: column;
            flex-direction: column;
    font-family: var(--font);
}
.release-details-item {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 10px;
    font-size: 17px;
    color: var(--color-font);
}

.meta-data {
    font-size: 25px;
    color: var(--color-font-special);
    font-family: var(--special-font);
}

.trailer-btn {
    position: relative;
    width: 100%;
    height: auto;
    margin-top: 10px;
    margin-left: 10px;
}

.trailer-btn button {
    width: auto;
    height: auto;
    font-size: 20px;
    background: none;
    border: none;
    color: white;
    font-family: var(--special-font);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    cursor: pointer;
    outline: none;
}

.trailer-btn button::before {
    content: "\f144";
    font-family: "Font Awesome";
    margin-right: 15px;
    font-size: 35px;
    transition: all 1s;
    color: white;
    outline: none;
}

.trailer-btn button:hover:before {
    color: var(--color-font-special);
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg)
}




.trailer {
    position: absolute;
    width: 100%;
    height: 100vh;
    z-index: 9999;
    overflow: hidden;
    -webkit-animation-name: fadeIn;
    animation-name: fadeIn;
    background-color: rgb(0, 0, 0);
    overflow: hidden;
}

.loading {
    position: absolute;
    width: 101%;
    height: 100vh;
    z-index: 999999;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: black;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    color: #ffffff;
}

.trailer > button {
    position: absolute;
    border: none;
    background: none;
    outline: none;
    color: aliceblue;
    width: 40px;
    height: 40px;
    top: 0;
    left: 0;
    margin: 10px;
    z-index: 999999;
}

.trailer > button::before {
    content: '\f00d';
    font-family: 'Font Awesome Light';
    position: relative;
    top: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 23px;
    cursor: pointer;
    width: 100%;
    height: 100%;
}

.trailer > button:hover {
    background-color: rgba(59, 59, 59, 0.712);
    border-radius: 50%;
}

.slick-slider, .slick-list, .slick-track {
    height: 100%;
}

.slick-slide {
    display: -webkit-flex !important;
    display: flex !important;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.item-video {
    width: 100%;
    margin-right: 20px;
    text-align: center;
    vertical-align: middle;
}

.video {
    width: 70%;
    height: 80vh;
    border: 1px solid rgb(24, 24, 24);
}

.item-video p{
    font-size: 15px;
    color: var(--color-font);
    font-family: var(--font);
}

.trailer:hover .slick-prev {
    opacity: 1;
    -webkit-transform: translateX(40px);
            transform: translateX(40px);
}

.trailer:hover .slick-next {
    opacity: 1;
    -webkit-transform: translateX(-60px);
            transform: translateX(-60px);
}

.slick-next, .slick-prev {
    position: absolute;
    color: var(--color-font);
    z-index: 999999;
    opacity: 0;
    transition: all 0.5s;
}

.slick-prev {
    -webkit-transform: translateX(30px);
            transform: translateX(30px);
}

.slick-next {
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
}

.slick-prev::before, .slick-next::before {
    font-size: 40px;
}

.slick-prev::before {
    content: '\f053';
    font-family: 'Font Awesome Light';
    font-size: 25px;
}

.slick-next::before {
    content: '\f054';
    font-family: 'Font Awesome Light';
    font-size: 25px;
}

