}

@-webkit-keyframes animate {
    0% {
        -webkit-transform: translateY(-0.5%);
                transform: translateY(-0.5%);
    }
    25% {
        -webkit-transform: translateY(0.5%);
                transform: translateY(0.5%)
    }
    50% {
        -webkit-transform: translateY(-0.5%);
                transform: translateY(-0.5%);
    }
    75% {
        -webkit-transform: translateY(0.5%);
                transform: translateY(0.5%);
    }
    100% {
        -webkit-transform: translateY(-0.5%);
                transform: translateY(-0.5%);
    }
}

@keyframes animate {
    0% {
        -webkit-transform: translateY(-0.5%);
                transform: translateY(-0.5%);
    }
    25% {
        -webkit-transform: translateY(0.5%);
                transform: translateY(0.5%)
    }
    50% {
        -webkit-transform: translateY(-0.5%);
                transform: translateY(-0.5%);
    }
    75% {
        -webkit-transform: translateY(0.5%);
                transform: translateY(0.5%);
    }
    100% {
        -webkit-transform: translateY(-0.5%);
                transform: translateY(-0.5%);
    }
}

