.search {
    position: relative;
    width: 53vw;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    margin: 0 0 40px 0;
    z-index: 9999;
}
.search-input {
    position: relative;
    width: 50%;
    height: auto;
}

.search-input input {
    position: relative;
    width: 100%;
    padding: 10px 5px;
    font-size: 18px;
    border: none;
    background: none;
    border-bottom: 1px solid white;
    color: var(--color-font);
}
.search-input input:focus{
    outline: none;
}

.list-suggest {
    width: 50%;
    max-height: calc(6*40px);
    position: absolute;
    list-style-type: none;
    background: rgba(0,0,0,.85);
    top: 100%;
    overflow-y: scroll;
    
}
.list-suggest::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #F5F5F5;
}
.list-suggest::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #000000;
	border: 3px solid #555555;
}
.list-suggest::-webkit-scrollbar {
    width: 5px;
	background-color: #F5F5F5;
}

.list-suggest li {
   color: var(--color-font);
   font-size: 16px;
   padding: 5px;
   height: 40px;
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: flex-start;
           justify-content: flex-start;
   -webkit-align-items: center;
           align-items: center;
   cursor: pointer;
}


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
@font-face {
    font-family: 'Font Awesome Light';
    src: url(/film-overview/static/media/fa-light-300.6ff36265.woff2);
}

@font-face {
    font-family: 'Font Awesome';
    src: url(/film-overview/static/media/fa-solid-900.7d49f26e.woff2);
}
:root {
    --color-font: white;
    --color-font-special: #00FC87;
    --title-font:'Roboto', sans-serif;
    --special-font: 'Quicksand', sans-serif;
    --font: 'Poppins', sans-serif;
}

.main {
    position: relative;
    width: 100%;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    overflow: hidden;
}

.main::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(rgba(0,0,0,.85) 15%,rgba(0,0,0,.2) 40%,#000 90%);
}
@media only screen and (max-width: 1300px) {
    .search {
        width: 70vw !important;
    }
    .search-input input {
        font-size: 17px;
    }
    .main-cart {
        width: 70vw;
        height: 650px;
    }
    .video {
        width: 80vw;
        height: 70vh;
    }
}

@media only screen and (max-width: 900px) {
    .search {
        width: 85vw !important;
    }
    .search-input input {
        font-size: 17px;
    }
    .main-cart {
        width: 85%;
        height: 650px;
    }
}

@media only screen and (max-width: 748px) {
    .search {
        width: 90vw !important;
        margin-bottom: 30px;
    }
    .search-input input {
        font-size: 17px;
    }
    .main-cart {
        width: 90%;
        height: 550px;
    }
    .cart-info h1 {
        font-size: 35px;
        margin: 5px 0;
    }
    .release-details-item {
        font-size: 15px;
        margin: 2px;
    }
    .meta-data {
        font-size: 20px;
    }
}



@media only screen and (max-width: 600px){
    .search {
        width: 95vw !important;
        margin-bottom: 15px;
    }
    .search-input input {
        font-size: 14px;
    }
    .main-cart {
        width: 95vw;
        height: 400px;
    }
    .cart-info {
        padding: 5px 10px;
    }
    .cart-info h1 {
        font-size: 25px;
        margin: 3px 0;
        overflow: hidden;
        text-overflow: clip;
        height: auto;
        line-height: 30px;
        -webkit-line-clamp: 2;
        display: -webkit-box;
        -webkit-box-orient: vertical;
    }
    .tagline {
        font-size: 13px;
        text-align: left;
        overflow: hidden;
        text-overflow: clip;
        height: auto;
        line-height: 15px;
        -webkit-line-clamp: 2;
        display: -webkit-box;
        -webkit-box-orient: vertical;
    }
    .overview {
        font-size: 11px;
        text-align: left;
        overflow: hidden;
        text-overflow: clip;
        height: 45px;
        line-height: 15px;
        -webkit-line-clamp: 3;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        margin-bottom: 5px;
    }
    .genre-list {
        font-size: 14px;
        text-align: left;
        overflow: hidden;
        text-overflow: ellipsis;
        height: 15px;
        line-height: 15px;
        -webkit-line-clamp: 1;
        display: -webkit-box;
        -webkit-box-orient: vertical;
    }
    .production-list {
        font-size: 12px;
        overflow: hidden;
        text-overflow: clip;
        height: 30px;
        line-height: 15px;
        -webkit-line-clamp: 2;
        display: -webkit-box;
        -webkit-box-orient: vertical;
    }
    .release-details-item {
        font-size: 11px;
        margin: 2px;
    }
    .meta-data {
        font-size: 13px;
    }
    .video {
        width: 100vw;
        height: 60vh;
    }
    .trailer-btn {
        margin-top: 5px;
        margin-left: 5px;
    }
    .trailer-btn button {
        font-size:13px;
    }
    .trailer-btn button::before {
        font-size:25px;
        margin-right: 10px;
    }
}

